/* 7. Single Post
/* ---------------------------------------------------------- */

.post-full-header {
    text-align: left;
}

.post-full-meta {
    align-items: center;
    justify-content: flex-start;
    margin-top: 2rem;
    margin-bottom:0;
    flex-wrap: wrap-reverse;
}

.post-full-header-date {
    display:inline-block;
    font-weight: 300;
    color: inherit;
    font-size:1.3rem;
    text-transform: uppercase;
    margin-bottom: 1rem;
}

.post-full-meta .author-profile-name {
    margin: 0 auto 1rem 0;
    font-size: 1.4rem;
}

.post-full-meta .author-profile-name a {
    color: inherit;
}

.post-full-meta .author-profile-image, .post-full-meta .avatar-wrapper {
    margin-right: 14px;
    width: 32px;
    height: 32px;
    margin-bottom:1rem;
}

.post-full-meta .date-divider {
    margin-bottom:1rem;
}

.post-full-header-tag {
    display: inline-block;
    font-size: 1.3rem;
    line-height: 1.15em;
    letter-spacing: .5px;
    text-transform: uppercase;
    margin-bottom:1rem;
    font-weight:500;

}


